                <img src="./../assets/card/gongxi_card.png" alt="">
              </div>
            </div>
            <img src="./../assets/small/icon_close.png" alt="" class="close"
                 @click="closeBg"/>
          </div>
        </div>

        <div class="bg-center12" v-if="contentShow == 'couponList'" @click.stop="">
          <div>
            <div>
              <!--<h3 class="tipTitle"><span></span><b></b></h3>-->
              <img src="http://res.catchme.com.cn/activity/card/my_bg.png" alt="" class="imgBg">
              <ul>
                <li v-for="item in activity_bounty.list">
