        <div class="bg-center13" v-if="contentShow == 'free'" @click.stop="">
          <div>
            <img @click.prevent="" :src="freeTipImg" alt="" class="imgBg" />
            <img src="./../assets/small/icon_close.png" alt="" class="close"
                 @click="closeBg"/>
