<template>
  <div class="quanprogress" :class="{'quan-version2':version2}">
    <p class="p1" @click="openActivityRule"></p>
    <p class="p2" @click="goExchange">:{{activity_bounty.list.length}}<span></span></p>
    <div>
      <img @click.prevent="" :src="myImg1.bgImg" alt="" class="imgBg">
      <img :src="myImg1.leftImg" alt="" class="ikang-top">
      <img :src="myImg1.textImg" alt="" class="text">
      <div class="progress-out">
        <div class="progress-in" :style="styleLong"></div>
      </div>
      <img v-if="activity_bounty.voucher.value <= activity_bounty.bounty && activity_bounty.voucher.value !== 0" class="ling" @click="goReceive" src="./../assets/card/press_ling.png" alt="">
      <div class="right" v-else=""><span>{{(activity_bounty.voucher.value - activity_bounty.bounty).toFixed(2)}}</span></div>
    </div>
  </div>
</template>

<script>
  import api from './../api/index'
  import CONFIG from './../config/index'
  import {mapState} from 'vuex'

  export default {
    name: 'quanprogress',
    data() {
      return {
        styleLong: '',
        isFirst: true,
//        img1:'http://res.catchme.com.cn/activity/card/ikang_short.png',
//        img2:'http://res.catchme.com.cn/activity/card/ikang_long.png',
        myImg1:{
          bgImg:'http://res.catchme.com.cn/activity/card/ikang_bg.png',
          leftImg:'http://res.catchme.com.cn/activity/card/xin.png',
          textImg:require('./../assets/card/text.png')
        },
        myImg2:{
          bgImg:'http://res.catchme.com.cn/activity/card/ikang_bg2.png',
          leftImg:'http://res.catchme.com.cn/activity/card/xin_long.png',
          textImg:require('./../assets/card/text2.png')
        },
        needle:0
    }
    },
    props: ['cinemaInfo'],
    computed: {
      ...mapState({
        activity_promocode: state => state.user.activity_promocode,
        activity_bounty: state => state.user.activity_bounty,
      })
    },
    mounted() {
      if (this.version2) {
        this.myImg1 = this.myImg2;
      }
      this.$store.dispatch('getActivityBounty').then((res)=>{
        if(res.bounty<res.voucher.value){
          var bili = res.bounty/res.voucher.value;
          this.styleLong = 'width:' + bili * 100 + '%';
        }else {
          this.styleLong = 'width:100%';
        }
      })
    },
    methods: {
      goExchange() {
        if (this.activity_bounty.list && this.activity_bounty.list.length === 0) {
          this.$emit('openTip', 'notExchange');
        } else {
          this.$emit('openTip', 'couponList');
        }
      },
      openActivityRule() {
        this.$emit('openTip', 'activityRule');
      },
      goReceive(){
        this.$emit('openTip','receive');
      }
    },
    watch: {
      activity_bounty(newActivity,oldActivity){
        if (this.isFirst) {
          this.isFirst = false;
          return;
        }
        //3
        if(newActivity.bounty === oldActivity.bounty){
          console.log('')
          setTimeout(()=>{
            this.$store.dispatch('getActivityBounty');
          },3000);
          return;
        }
        if(newActivity.bounty<newActivity.voucher.value){
          var bili = newActivity.bounty/newActivity.voucher.value;
          this.styleLong = 'width:' + bili * 100 + '%';
        }else {
          this.styleLong = 'width:100%';
        }
        if(newActivity.voucher.value <= newActivity.bounty ){
          this.goReceive();
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  $progressColor:#fef52e;
  $progressBgColor:#354dc8;
  .quanprogress {
    width: 506px;
    position: absolute;
    right:20px;
    bottom: 0;
    >div{
      position: relative;
      .imgBg{
        width: 100%;
        display: block;
      }
      .ikang-top{
        position: absolute;
        width: 178px;
        bottom:0;
        left: 0;
        z-index: 444;
      }
      .text{
        position: absolute;
        left: 85px;
        top:50px;
        width: 341px;
        z-index: 666;
      }
      .right{
        padding: 6px 20px 0 15px;
        height: 56px;
        background: url("./../assets/card/haicbg.png") no-repeat;
        background-size: 100% 100%;
        position: absolute;
        right: 8px;
        bottom: 6px;
        font-size: 20px;
        color: $progressColor;
        font-weight: 600;
        span{
          font-size: 28px;
        }
      }
    }
    .p1{
      position: absolute;
      text-align: center;
      height: 30px;
      line-height: 18px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 18px;
      background: rgba(8,8,85,0.3);
      padding: 0 10px;
      color: #fff;
      left: 8px;
      top:8px;
      border-radius: 15px;
      z-index: 555;
    }
    .p2{
      height:36px;
      position: absolute;
      right: 8px;
      top:8px;
      z-index: 2;
      color: $progressColor;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      font-size: 22px;
      line-height: 32px;
      background: $progressBgColor;
      border-radius: 18px;
      padding: 0 12px;
      border: 2px solid #4cf0f8;
     span{
       width: 23px;
       height: 16px;
       float: right;
       background: url("./../assets/card/icon_go_quan.png") no-repeat;
       background-size: 100% 100%;
       margin: 0 0 0 3px;
     }
    }
  }


  .ling{
    width: 112px;
    height: 84px;
    position: absolute;
    right: -15px;
    bottom:-10px;
  }
  .progress-out {
    width: 351px;
    height: 30px;
    background:url("./../assets/card/progress_bg.png") no-repeat;
    background-size: 100% 100%;
    border-radius: 15px;
    overflow: hidden;
    position: absolute;
    left: 120px;
    bottom:16px;
    padding:0;
  }

  .progress-in {
    width: 0;
    height: 100%;
    background: #ffea3b;
    border-radius: 0 15px 15px 0;
    border-right: 3px solid #63bdfc;
    background: url("./../assets/card/progress.png") no-repeat;
    transition: all 0.5s;
    background-size: auto 100%;
  }

  .tip1{
    position: absolute;
    font-size: 30px;
    color: #fff;
    top:34px;
    left: 154px;
    line-height: 38px;
  }

  .quan-version2{
    /*width: 492px;*/
    >div{
      .ikang-top{
        position: absolute;
        width: 204px;
        bottom:0;
        left: 0;
        z-index: 444;
      }
      .text{
        position: absolute;
        left: 85px;
        top:50px;
        width: 352px;
        z-index: 666;
      }
    }
    .p1{
      font-size: 20px;
      line-height: 20px;
      height: 28px;
      border-radius: 14px;
      background: rgba(8,8,85,0.2);
    }
    .p2{
      /*top:8px;*/
    }
    .tip1 {
      top:44px;
      font-size: 34px;
    }
      >div{
      .right{
        font-size: 22px;
        bottom: 21px;
        span{
          font-size: 32px;
        }
      }
    }
    .progress-out {
      bottom:31px;
    }
    .ling{
      width: 108px;
      height: 82px;
      bottom: 0;
    }
    }

</style>



